.popup {
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  justify-content: center;
  align-content: center;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.65s ease-in-out, visibility 0s ease 0.65s;
}